:root {
    --primary-color: #23a4fb;
    --warning-color: #ff9800;
    --danger-color: #f44336;
    --success-color: #4caf50;
    --info-color: #00acc1;
    --rose-color: #e91e63;
    --grey-color: #999999;
}

html,body {
    height: 100%;
    color:white;
    background-color: #424242!important;
    margin: 0;
    padding: 0;
    position: relative;
    overflow-x: hidden;
}

.dark-thumb-vertical, .dark-thumb-horizontal{
    background-color: rgba(255,255,255,0.2);
    border-radius: 3px;
}

.primary-top-to-bottom {
    border-top: 4px solid #23a4fb;
    border-top: 4px solid var(--primary-color);
    background-image:
            linear-gradient(#23a4fb, transparent),
            linear-gradient(#23a4fb, transparent);
    background-image:
            linear-gradient(var(--primary-color), transparent),
            linear-gradient(var(--primary-color), transparent);
    background-size: 4px 100%;
    background-position: 0 0, 100% 0;
    background-repeat: no-repeat;
}

